.registration {
    width: 500px;
    margin: 0 auto;
    padding: 30px;
    box-shadow: 3px 0 12px rgba(0, 0, 0, 0.4)  ; 
	-webkit-box-shadow: 3px 0 12px rgba(0, 0, 0, 0.4)  ; 
	-moz-box-shadow: 3px 0 12px rgba(0, 0, 0, 0.4)  ; 
}
.registration__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}
.registration__logo-text {
    color: #7b7ad8;
    margin-left: 8px;
}
.input__field {
    width: 100%;
    padding: 5px;
    border: none;
    outline: none;
    margin-bottom: 25px;
}
.registration__btn {
    width: 100%;
    background: #7b7ad8;
    padding: 10px;
    border: 0px;
    color: white;
}
.registration__or{
    display: inline-block;
    position: relative;
    padding:20px 10px;
} 
.registration__or::after{
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 100%;
    width: 100px;
    height: 3px;
    background: #7b7ad8;
}
.registration__or::before{
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 100%;
    width: 100px;
    height: 3px;
    background: #7b7ad8;
}
.registration__account {
    text-align: center;
    display: block;
    text-decoration: none;
    color: #7b7ad8;
    padding: 30px 0px;
}